@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
html{height:100%}body{margin:0;font-family:-apple-system,BlinkMacSystemFont,"Segoe UI","Roboto","Oxygen","Ubuntu","Cantarell","Fira Sans","Droid Sans","Helvetica Neue",sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;height:100%}code{font-family:source-code-pro,Menlo,Monaco,Consolas,"Courier New",monospace}
.App{min-height:100%;background:#020024;background:linear-gradient(5deg, #020024 0%, #4b4ad4 1%, #00d4ff 100%);background-size:cover;display:flex;flex-direction:column}
.Home{height:100%}
.list{display:flex;align-items:center;justify-content:space-between;flex-wrap:wrap}
*{text-decoration:none}.card{width:300px;border:2px solid #ff6b81;background-color:#fff;border-radius:10px;display:block;margin:20px 30px;box-shadow:4px 4px 5px 0px rgba(0,0,0,.29);-webkit-box-shadow:4px 4px 5px 0px rgba(0,0,0,.29);-moz-box-shadow:4px 4px 5px 0px rgba(0,0,0,.29)}.card-container{max-width:20%}.card-container-h2{font-size:30px;color:#1e90ff;display:block;margin:auto 100px}



.header{background-color:#2f3542;padding:0;box-shadow:-2px 5px 5px 0px rgba(0,0,0,.31);-webkit-box-shadow:-2px 5px 5px 0px rgba(0,0,0,.31);-moz-box-shadow:-2px 5px 5px 0px rgba(0,0,0,.31)}.container{max-width:1120px;width:100%;margin:0;display:block}.header-list{margin:0;list-style:none;display:flex;justify-content:center;align-items:center}.header-list-item{padding:1.3% 5%;color:rgba(255,255,255,.892);text-decoration:none;display:flex;font-size:18px;transition:.2s All;font-family:"Poppins",sans-serif}.header-list-item:hover{color:#fff}
.footer{width:100%;height:40px;text-align:center;color:#fff}

